import React, { Component, Suspense, lazy } from 'react'
import { Switch, Route } from "react-router-dom"
import $ from 'jquery'
import Nav from './components/Nav'
import Footer from './components/Footer'
import DownloadOptions from './components/DownloadOptions'
// import Support from './components/Support'
import './libs/libs.css'
import './index.css'
import './App.css'
import './components/Demo/Demo.css'
import './Media.css'

const Home = lazy(() => import('./components/Home'))
const Faq = lazy(() => import('./components/Faq'))
const Demo = lazy(() => import('./components/Demo/MultiStream'))
const Terms = lazy(() => import('./components/Terms'))
const Privacy = lazy(() => import('./components/Privacy'))
const Contacts = lazy(() => import('./components/Contacts'))
const PageNotFound = lazy(() => import('./components/404/404'))

const api = require('twitch-api-v5')
api.clientID = 'lxtgfjpg12cxsvpy32vg5x7a1ie6mc'

class App extends Component {

  componentDidMount() {
    let w = $(window).scroll(() => {
      if (w.scrollTop() > 840) {
        $(".nav.home-nav").addClass("scrolled")
      } else {
        $(".nav.home-nav").removeClass("scrolled")
      }
    })
  }

  render() {
    return (
          <div className="container-fluid">
            <Nav />
            <Suspense fallback={<div />}>
              <Switch>
                <Route path="/" exact component={props => <Home />} />
                <Route path="/faq" component={props => <Faq />} />
                <Route path="/contacts" component={props => <Contacts />} />
                <Route path= "/demo" component={props => <Demo api={api} />} />
                <Route path="/terms" component={props => <Terms />} />
                <Route path="/privacy" component={props => <Privacy />} />
                {/* <Route path="/support" component={props => <Support />} /> */}
                <Route component={props => <PageNotFound />} />
              </Switch>
            </Suspense>
            <DownloadOptions />
            <Footer />
          </div>
    )
  }
}

export default App
